<script>
import axios from 'axios';
import LocationTypeAhead from '@/components/LocationTypeAhead.vue'

export default {
  name: 'Filters',
  props: {
    filters: Object
  },
  components: {
    LocationTypeAhead
  },
  mounted(){
    this.fetchGenres();
  },
  data: function() {
    return {
      genresData: [],
      locationsData: [],
      alphabet: 'abcdefghijklmnopqrstuvwxyz#'.split(''),
    }
  },
  methods: {
    fetchGenres(){
        axios.get('/api/genres')
        .then((response) => (
            this.genresData = response.data
        ))
    },
    shuffleRandom(){
      this.$parent.fetchRandom();
    }
  }
}
